#Shortcut{z-index:1;position:sticky;top:0;margin-top:-2px;border-top:1px solid #000;height:200vh;background-color:#000;display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 1fr);gap:1px}#Shortcut>a{z-index:1;position:sticky;top:0;width:100%;height:100%;border-top:1px solid #000;background-color:#fff;overflow:hidden}#Shortcut>a:nth-child(1){margin-top:-1px;background:url(https://buly.kr/AlkAbAR) center/cover no-repeat}#Shortcut>a:nth-child(1)::after{content:"오 드 퍼퓸";position:absolute;right:40px;top:40px}#Shortcut>a:nth-child(1):hover{background:url(https://buly.kr/FAd77YO) center/cover no-repeat}#Shortcut>a:nth-child(2){margin-top:-1px;background:url(https://buly.kr/15OUeI9) center/cover no-repeat}#Shortcut>a:nth-child(2)::after{content:"솔리드 퍼퓸";position:absolute;right:40px;top:40px}#Shortcut>a:nth-child(2):hover{background:url(https://buly.kr/9BVLlp2) center/cover no-repeat}#Shortcut>a:nth-child(3){padding-top:100px;position:relative;background:url(https://buly.kr/5JMftwa) center bottom/50% no-repeat;background-color:#fff;display:flex;flex-direction:column;align-items:center;justify-content:flex-start}#Shortcut>a:nth-child(3) span{font-size:40px;font-family:"Elegance";letter-spacing:1px}#Shortcut>a:nth-child(3) h1{margin-top:60px}#Shortcut>a:nth-child(3) p{text-align:center}#Shortcut>a:nth-child(4){background:url(../../public/img/kakao.jpg) center/cover no-repeat}